@use '../../color-palette.scss' as p;

div.about-me{
  color: p.$OnBackground;
  font-family: 'Nunito', sans-serif;
  font-size: 20px;

  .background-img{
    height: calc(100vh - 60px);
    background-image: url('../../res/aboutme/background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: blur(2.5px); /* Safari 6.0 - 9.0 */
    filter: blur(2.5px);
  }

  .description{
    border-radius: 20px;
    padding: 0 2% 1% 2%;
    background-color: p.$Surface;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -60%);
  }

  .img1{
    position: absolute;
    top: 30%;
    left: 20%;
    height: 400px;
  }

  .img2{
    position: absolute;
    top: 45%;
    left: 75%;
    height: 400px;
  }

  .img3{
    position: absolute;
    top: 15%;
    left: 65%;
    height: 400px;
  }

  img{
    border-radius: 20px;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    pointer-events: none;
  }
}